<template>
    <div class="hietory-table">
      <base-table :data="history" :columns="columns"
                  :totalSize="totalSize"
                  :pageSizeOpts="pageSizeOpts"
                  :placement="placement"
                  :current="getHistoryData.page"
                  @changePage="changePage"
                   @changeTable="changeTable">
        <i-select v-model="model2" size="small" style="width:100px;margin-right: 10px" slot="sort" placeholder="排序" @on-change="sort">
        <i-option v-for="item in List" :value="item.value" :key="item.value">{{ item.label }}</i-option>
      </i-select>

      </base-table>
    </div>
</template>

<script>
import BaseTable from "../base-components/base-table.vue";
import { getHistoryUrl } from  '../api/history-table';
import ISelect from 'iview/src/components/select/select.vue';
import IOption from 'iview/src/components/select/option.vue';

  export default {
  data(){
    return{
      history:[],
      columns:[
        {
        type: 'selection',
        width: 60,
        align: 'center'
      },
        {
          title: 'qqId',
          key: 'qqid',
          sortable: true
        },
        {
          title: 'myId',
          key: 'myid',
          sortable: true
        },
        {
          title: 'Jitter',
          key: 'jitter',
          filters: [
            {
              label: '大于0.15%',
              value: 1
            },
            {
              label: '小于0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.jitter>'0.15%';
            } else if (value === 2) {
              return row.jitter < '0.5%';
            }
          }
        },
        {
          title: 'Shimmer',
          key: 'shimmer',
          filters: [
            {
              label: '大于0.15%',
              value: 1
            },
            {
              label: '小于0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.shimmer>'0.15%';
            } else if (value === 2) {
              return row.shimmer < '0.5%';
            }
          }
        },
        {
          title: 'HNR',
          key: 'hnr',
          filters: [
            {
              label: '大于0.15%',
              value: 1
            },
            {
              label: '小于0.5%',
              value: 2
            }
          ],
          filterMultiple: false,
          filterMethod (value, row) {
            if (value === 1) {
              return row.hnr>'0.15%';
            } else if (value === 2) {
              return row.hnr < '0.5%';
            }
          }
        },
        {
          title: 'time',
          key: 'time',
        }],
      totalSize:0,                      //数据的总条数
      pageSizeOpts:[10,30,50,100],      //可以选择的每页显示的条数
      placement:"top",                  //条数切换弹窗的展开方向
      List: [
        {
          value: 'default',
          label: '默认排序'
        },
        {
          value: 'jitterAsc',
          label: 'Jitter升序'
        },
        {
          value: 'jitterDes',
          label: 'Jitter降序'
        },
        {
          value: 'shimmerAsc',
          label: 'Shimmer升序'
        },
        {
          value: 'shimmerDes',
          label: 'Shimmer降序'
        },
        {
          value: 'hnrAsc',
          label: 'Hnr升序'
        },
        {
          value: 'hnrDes',
          label: 'Hnr降序'
        },
        {
          value: 'timeAsc',
          label: '时间升序'
        },
        {
          value: 'timeDes',
          label: '时间降序'
        }
      ],
      model2: '',
      getHistoryData:{                //排序
        page:1,                            // 默认第一页查询
        size:10,                           // 默认10页/条
        sortDirections: [],               // 默认顺序
        sortProperties: []               // 默认查询字段
      },
    }
  },
    created(){
      this.getHistory();
    },
    mounted(){
  },
    methods:{
      getHistory(){
        getHistoryUrl(this.getHistoryData).then((res) =>{             //默认查询
          this.history = res.result[0].content;
          this.totalSize = res.result[0].totalPages * res.result[0].size;
        })
      },
      changePage(PageSize){                        //当前选择的是几 条/页
          this.getHistoryData.size = PageSize;
          this.getHistory();
      },
      changeTable(index){                         //分页查询
          this.getHistoryData.page = index;
          this.getHistory();
      },
      sort(value){
        this.getHistoryData.sortDirections = [];
        this.getHistoryData.sortProperties = [];
        let direction = value === 'jitterAsc' || value === 'shimmerAsc' || value === 'hnrAsc' || value === 'timeAsc' ? 'ASC' : value === 'default' ? '' : 'DESC';
        let properties = value === 'jitterAsc' || value === 'jitterDes' ? 'jitter' : value === 'shimmerAsc' || value === 'shimmerDes' ? 'shimmer'
         : value === 'timeAsc' || value === 'timeDes' ? 'time' : value === 'default' ? '' : 'hnr';
        this.getHistoryData.page = 1;
        this.getHistoryData.sortDirections.push(direction);
        this.getHistoryData.sortProperties.push(properties);
        this.getHistory();
      }
    },
    watch:{
      //监听changePageSize变化加载数据
      changePageSize(){
        this.changeTable(1)
      }
    },
  components:{
    BaseTable,
    ISelect,
    IOption
  }
  }
</script>
<style lang="scss">

  .ivu-select-selected-value{
    width: 90px;
  }
</style>



